import React, { useState } from "react";
import classNames from "classnames";
import { SectionTilesProps } from "../../utils/SectionProps";
import SectionHeader from "./partials/SectionHeader";
import Image from "../elements/Image";
import Modal from "../elements/Modal";

const propTypes = {
  ...SectionTilesProps.types,
};

const defaultProps = {
  ...SectionTilesProps.defaults,
};
const FeaturesTiles = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {
  const [url, setUrl] = useState("");
  const [videoModalActive, setVideomodalactive] = useState(false);
  const openModal = (e) => {
    e.preventDefault();
    setVideomodalactive(true);
  };

  const closeModal = (e) => {
    e.preventDefault();
    setVideomodalactive(false);
  };

  const outerClasses = classNames(
    "features-tiles section",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "features-tiles-inner section-inner pt-0",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );

  const tilesClasses = classNames(
    "tiles-wrap center-content",
    pushLeft && "push-left"
  );

  const sectionHeader = {
    title: "  ",
    paragraph:
      "            ",
  };

  return (
    <section {...props} className={outerClasses}>
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={tilesClasses}>
            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16">
                    <a
                      data-video="https://streamable.com/e/uu3dlv"
                      href="#0"
                      aria-controls="video-modal"
                      onClick={(e) => {
                        setUrl("https://streamable.com/e/uu3dlv");
                        openModal(e);
                      }}
                    >
                      <Image
                        className="has-shadow"
                        src={"https://i.imgur.com/WW0Wvby.png"}
                        alt=""
                        width={896}
                        height={504}
                      />
                    </a>
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">  </h4>
                  <p className="m-0 text-sm"> 6 </p>
                  <p className="m-0 text-sm2">
                    [       ]
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-bottom"
              data-reveal-delay="200"
            >
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16">
                    <a
                      data-video="https://streamable.com/e/avje5f"
                      href="#0"
                      aria-controls="video-modal"
                      onClick={(e) => {
                        setUrl("https://streamable.com/e/avje5f");
                        openModal(e);
                      }}
                    >
                      <Image
                        className="thumbnail"
                        src={"https://i.imgur.com/SqVO8jO.jpg"}
                        alt=""
                        width={896}
                        height={504}
                      />
                    </a>
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8"> [  ]</h4>
                  <p className="m-0 text-sm">0%  </p>
                  <p className="m-0 text-sm2">
                    [       ]
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-bottom"
              data-reveal-delay="400"
            >
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16">
                    <a
                      data-video="https://streamable.com/e/ogiph"
                      href="#0"
                      aria-controls="video-modal"
                      onClick={(e) => {
                        setUrl("https://streamable.com/e/ogiph");
                        openModal(e);
                      }}
                    >
                      <Image
                        className="thumbnail"
                        src={"https://i.imgur.com/Y7JJ3k4.png"}
                        alt=""
                        width={896}
                        height={504}
                      />
                    </a>
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8"> [  ]</h4>
                  <p className="m-0 text-sm">
                         
                  </p>
                  <p className="m-0 text-sm2">
                    [       ]
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16">
                    <a
                      data-video="https://player.vimeo.com/video/735397818"
                      href="#0"
                      aria-controls="video-modal"
                      onClick={(e) => {
                        setUrl("https://player.vimeo.com/video/735397818");
                        openModal(e);
                      }}
                    >
                      <Image
                        className="thumbnail"
                        src={"https://i.imgur.com/SpO0HHY.jpg"}
                        alt=""
                        width={896}
                        height={504}
                      />
                    </a>
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8"> [  ]</h4>
                  <p className="m-0 text-sm">
                    ,ESP    {" "}
                  </p>
                  <p className="m-0 text-sm2">
                    [       ]
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-bottom"
              data-reveal-delay="200"
            >
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16">
                    <a
                      data-video="https://streamable.com/dmz8nx"
                      href="#0"
                      aria-controls="video-modal"
                      onClick={(e) => {
                        setUrl("https://streamable.com/dmz8nx");
                        openModal(e);
                      }}
                    >
                      <Image
                        className="thumbnail"
                        src={"https://i.imgur.com/ArIMPZM.jpg"}
                        alt="AI"
                        width={896}
                        height={504}
                      />
                    </a>
                  </div>
                </div>

                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8"> [ AI ]</h4>
                  <p className="m-0 text-sm">
                         
                  </p>
                  <p className="m-0 text-sm2">
                    [       ]
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-bottom"
              data-reveal-delay="400"
            >
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16">
                    <a
                      data-video="https://streamable.com/e11cqb"
                      href="#0"
                      aria-controls="video-modal"
                      onClick={(e) => {
                        setUrl("https://streamable.com/e11cqb");
                        openModal(e);
                      }}
                    >
                      <Image
                        className="thumbnail"
                        src={"https://i.imgur.com/lDA4gwE.png"}
                        alt=""
                        width={896}
                        height={504}
                      />
                    </a>
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8"> [  ]</h4>
                  <p className="m-0 text-sm">
                       
                  </p>
                  <p className="m-0 text-sm2">
                    [       ]
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Modal
        id="video-modal"
        show={videoModalActive}
        handleClose={closeModal}
        video={url}
        videoTag="iframe"
      />
    </section>
  );
};

FeaturesTiles.propTypes = propTypes;
FeaturesTiles.defaultProps = defaultProps;

export default FeaturesTiles;
